.navBar {
    display: none;
    justify-content: space-around;
    align-items: center;
    height: 200px;
    width: 100%;
  }
  .navBar_menus {
    display: block;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    margin: auto;
  }
  .navBar_menus_menu {
    color: #d6d6d6;
    text-decoration: none;
    display: block;
    margin: 0 auto;
    display: flex;
    width: 100%;
    flex-flow: column wrap;
  }
  @media screen and (max-width: 768px) {
    .navBar {
      justify-content: space-between;
      display: block;

    }
    .navBar_menus {
      display: block;
      list-style: none;
      justify-content: space-around;
      width: 100%;
      margin: auto;
    }
    .navBar_menus_menu {
      color: #d6d6d6;
      text-decoration: none;
      display: block;
      margin: 0 auto;
      display: flex;
      height: 35px;
      flex-flow: column wrap;
    }
  }
  