  .navBarW {
    display: block;
    justify-content: space-around;
    height: 50px;
    border-bottom: 1px solid #d1d8e4;
    width: 86%;
    padding-left: 2vw;
    padding-bottom: 1vw;
    padding-right: 2vw;
  }
  .navBar_menusW {
    display: flex;
    list-style: none;
    justify-content: space-around;
    width: 100%;
    margin: auto;
  }
  .navBar_menus_menuW a {
    color: #d6d6d6;
    text-decoration: none;
  }
  @media screen and (max-width: 768px) {
    .navBarW {
      justify-content: space-between;
      display: none;

    }
  }
  